import React from "react";
import cn from "classnames";
import design from "../../../Profile.module.css";
import Loader from "../../../../assets/Loader/Loader";

class PostProfileName extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        if(!this.props.profile){
            return <Loader />
        }
        return (
            <div className={cn(design.post_block__profile_name)}>
                <h3>{this.props.profile.fullName ? this.props.profile.fullName : this.props.postProfileName.name}</h3>
            </div>
        )
    }

}

export default PostProfileName;