import design from './renderField.module.css'

const RenderField = ({ input, meta: { error, touched }, ...props }) => {
    const hasError = error && touched
    return (
        <>
            {props.children}
            {hasError && <div className={design.errorText}>
                {error}
            </div>}
        </>
    )
}

export const Textarea = (props) => {
    const { input, ...restProps } = props;
    return <RenderField {...props}><textarea {...input} {...restProps} /></RenderField>
}

export const Input = (props) => {
    const { input, ...restProps } = props;
    return <RenderField {...props}><input {...input} {...restProps} /></RenderField>
}