import React from "react";
import cn from 'classnames';
import design from "../Profile.module.css";
import { Field, reduxForm } from "redux-form";
import { Input } from "../../assets/RenderField/renderField";
import { maxLengthField } from "../../assets/validators/validators";


const maxLengthField30 = maxLengthField(30)

let ProfileWritePost = props => {
    return (
        <form className={cn(design.write_post)} onSubmit={props.handleSubmit}>
            <Field className={cn(design.write_post__input)} type="text" placeholder="write new post" component={Input} name='addPostText' validate={[maxLengthField30]} />
            <button type='submit' className={cn(design.write_post__button)} >Publish</button>
        </form>
    )
}

export default reduxForm({ form: 'addNewPostForm' })(ProfileWritePost);