import React from "react";
import cn from "classnames";
import design from "../../../Profile.module.css";
import Loader from "../../../../assets/Loader/Loader";

class PostProfileLogo extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        if(!this.props.profile){
            return <Loader />
        }
        return (
            <div className={cn(design.post_block__profile_logo)}>
                <img src={this.props.profile.photos.small? this.props.profile.photos.small : this.props.postProfileLogo.src} alt="" />
            </div>
        )
    }

}

export default PostProfileLogo;