import React, { useEffect, useState } from "react";
import cn from "classnames"
import design from "../Profile.module.css";
import { Field, reduxForm } from "redux-form";
import { Input } from "../../assets/RenderField/renderField";

let StatusField = (props) => {
    let { status, onStatusChange, DeactivateEditStatus } = props;
    return (
        <form className={cn(design.profile_status__form)}>
            <Field className={cn(design.profile_status__input)} type="text" name="updateStatustext" component={Input} autoFocus={true} onBlur={DeactivateEditStatus} value={status} onChange={onStatusChange} />
        </form>
    )
}

StatusField = reduxForm({ form: 'status' })(StatusField);

const ProfileStatus = (props) => {
    const [editMode, setEditMode] = useState(false)
    const [status, setStatus] = useState(props.profileStatus)

    useEffect(() => {
        setStatus(props.profileStatus)
    }, [props.profileStatus])

    const ActivateEditStatus = () => {
        setEditMode(true)
    }

    const DeactivateEditStatus = () => {
        setEditMode(false)
        props.updateProfileStatus(status)
    }

    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value)
    }
    if (props.userAuthId != props.currentUserIdProfile) {
        return <div className={cn(design.profile_status)} >
            <div className={cn(design.profile_status__paragraph)}>
                <p>{status || '-------'}</p>
            </div>
        </div>
    }
    else if(props.userAuthId == props.currentUserIdProfile){
        return (
            <div className={cn(design.profile_status)} >
                <span>Status: </span>
                {!editMode &&
                    <div className={cn(design.profile_status__paragraph)}>
                        <p onDoubleClick={ActivateEditStatus}>{status || '-------'}</p>
                    </div>
                }
                {
                    editMode &&
                    <StatusField onStatusChange={onStatusChange} status={status} DeactivateEditStatus={DeactivateEditStatus} />
                }
            </div >
        )
    }
}

export default ProfileStatus;