import React from "react";
import cn from "classnames";
import design from "../../Profile.module.css"
import PostProfileLogo from "./PostProfileLogo/PostProfileLogo";
import PostProfileName from "./PostProfileName/PostProfileName";

class PostAuthor extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className={cn(design.authors_post)}>
                <PostProfileLogo postProfileLogo={this.props.postProfileLogo} profile={this.props.profile} />
                <PostProfileName postProfileName={this.props.postProfileName} profile={this.props.profile} />
            </div>
        )
    }

}

export default PostAuthor;