import React from "react";
import { connect } from "react-redux";
import Profile from "./Profile";
import { useLocation, useNavigate, useParams, } from "react-router-dom";
import { requestProfile, requestProfileStatus, updateProfileStatus, putNewProfileLogo } from '../redux/profile-reducer';
import { withAuthRedirect } from "../hoc/withAuthRedirect";
import { compose } from "redux";
import { getProfilePage, getUserAuthId, getUserProfile, getProfileStatus } from "../redux/profile-selectors";

function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }

    return ComponentWithRouterProp;
}

class ProfileContainer extends React.Component {
    constructor(props) {
        super(props);
    }

    userDataRequest = () => {
        var userId = this.props.router.params.userId;
        if (!userId) {
            userId = this.props.userAuthId;
            window.location.pathname += `/#/profile${userId}`
        }
        this.props.requestProfile(userId);
        this.props.requestProfileStatus(userId);
    }

    componentDidMount() {
        this.userDataRequest()
    }

    componentDidUpdate(prevProps) {
        if (prevProps.router.params.userId != this.props.router.params.userId) {
            this.userDataRequest()
        }
    }

    render() {
        var userId = this.props.router.params.userId;
        return (
            <Profile putNewProfileLogo={this.props.putNewProfileLogo}
                updateProfileStatus={this.props.updateProfileStatus}
                profilePage={this.props.profilePage}
                profile={this.props.profile}
                profileStatus={this.props.profileStatus}
                userAuthId={this.props.userAuthId}
                currentUserIdProfile={userId} />
        )
    }

}

const mapStateToProps = (state) => {
    return {
        profilePage: getProfilePage(state),
        profile: getUserProfile(state),
        profileStatus: getProfileStatus(state),
        userAuthId: getUserAuthId(state),
    }
}

export default compose(
    connect(mapStateToProps, { requestProfile, requestProfileStatus, updateProfileStatus, putNewProfileLogo }),
    withRouter,
    withAuthRedirect
)(ProfileContainer)