import React from "react";
import {addPostActionCreator}  from '../../redux/profile-reducer';
import ProfileWritePost from "./ProfileWritePost";
import { connect } from "react-redux";


class ProfileWritePostContainer extends React.Component{

    onAddPost=(values)=>{
        this.props.addPostActionCreator(values.addPostText)
    }
    
    render(){
        return(
            <ProfileWritePost onSubmit={this.onAddPost}/>
        )
    }
}


export default connect(null, {addPostActionCreator} )(ProfileWritePostContainer);