import React from "react";
import cn from "classnames";
import design from "../Profile.module.css";
import Loader from "../../assets/Loader/Loader";

class ProfileLogo extends React.Component {
    constructor(props) {
        super(props);
    }

    onPhotoSelected = (e) => {
        if (e.target.files.length) {
            this.props.putNewProfileLogo(e.target.files[0])
        }
    }

    render() {
        if (!this.props.profile) {
            return <Loader />
        }
        if (this.props.currentUserIdProfile != this.props.userAuthId) {
            return (
                <div className={cn(design.profile_logo)}>
                    <div className={design.change_logo__wrapper}>
                        <img src={this.props.profile.photos.small ? this.props.profile.photos.small : this.props.profileDefaultLogo} alt="" />
                    </div>
                </div >
            )
        }
        else if (this.props.currentUserIdProfile == this.props.userAuthId) {
            return (
                <div className={cn(design.profile_logo)}>
                    <div className={design.change_logo__wrapper}>
                        <img src={this.props.profile.photos.small ? this.props.profile.photos.small : this.props.profileDefaultLogo} alt="" />
                        <input type='file' id='put-logo' onChange={this.onPhotoSelected} className={design.change_logo__input} />
                        <label htmlFor="put-logo" className={design.change_logo__label}></label>
                    </div>
                </div >
            )
        }
    }

}

export default ProfileLogo;