import React from "react";
import cn from "classnames";
import design from "../Profile.module.css"
import PostAuthor from "./PostAuthor/PostAuthor";


class ProfilePosts extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className={cn(design.profile_posts)}>
                <h2 className={cn(design.profile_posts__title)}>Posts</h2>
                {this.props.posts.map(post=>
                    <div className={cn(design.post_block)} key={post.postId}>
                        <PostAuthor postProfileLogo={this.props.postProfileLogo} postProfileName={this.props.postProfileName} profile={this.props.profile} />
                        <p className={cn(design.post_message)}>{post.message}</p>
                    </div>
                )} 
            </div>
        )
    }

}

export default ProfilePosts;