import React from "react";
import design from "./Profile.module.css"
import ProfileStatus from "./ProfileStatus/ProfileStatus";
import ProfileLogo from "./ProfileLogo/ProfileLogo";
import ProfileName from "./ProfileName/ProfileName";
import ProfilePosts from "./ProfilePosts/ProfilePosts";
import ProfileWritePostContainer from "./ProfileWritePost/ProfileWritePostContainer";

class Profile extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className={design.profile}>
                <ProfileStatus currentUserIdProfile = {this.props.currentUserIdProfile} userAuthId = {this.props.userAuthId} profileStatus={this.props.profileStatus} updateProfileStatus={this.props.updateProfileStatus} />
                <ProfileLogo currentUserIdProfile = {this.props.currentUserIdProfile} userAuthId = {this.props.userAuthId} profileDefaultLogo={this.props.profilePage.profileLogo.src} profile={this.props.profile} putNewProfileLogo={this.props.putNewProfileLogo} />
                <ProfileName profileDefaultName={this.props.profilePage.profileName.name} profile={this.props.profile} />
                <ProfileWritePostContainer />
                <ProfilePosts posts={this.props.profilePage.posts} postProfileLogo={this.props.profilePage.profileLogo} postProfileName={this.props.profilePage.profileName} profile={this.props.profile} />
            </div>
        )
    }

}


export default Profile;